import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  Select,
  Row,
  Col,
  Divider,
  DatePicker,
  Tooltip
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/vatOutReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";

import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectSI = "/dashboard/sales/sales-invoices";
const redirectST = "/dashboard/sales/sales-returns";
const redirectRaptorSI = "/dashboard/raptor/sales-invoices";
const redirectRaptorST = "/dashboard/raptor/sales-returns";
const redirectPosSI = "/dashboard/pos/sales-invoices";
const redirectPosST = "/dashboard/pos/sales-returns";

class VatOutReport extends Component {
  state = {
    filter: {
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_location: null
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getVatOutReports } = this.props;
    getVatOutReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.vatOutReport.error) {
      notification("error", this.props.vatOutReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.vatOutReport.success) {
      notification("success", this.props.vatOutReport.success);
      this.props.clearSuccess();
    }
  }

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getVatOutReports } = this.props;
    //const { current_page } = this.props.vatOutReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getVatOutReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getVatOutReports } = this.props;
    this.setState({
      filter: {
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_location: null
      }
    });
    getVatOutReports();
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "15%",
        render: (text, row) => {
          if (row.title && row.title === "Title") {
            return {
              children: (
                <span>
                  <strong>{text}</strong>
                </span>
              ),
              props: {
                colSpan: 7
              }
            };
          } else if (row.title && row.title === "Total") {
            return {
              children: (
                <span>
                  <strong>{text}</strong>
                </span>
              ),
              props: {
                colSpan: 5
              }
            };
          } else {
            return <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>;
          }
        }
      },
      {
        title: getLanguage({ id: "purchaseInvoices.taxInvoiceNo" }),
        dataIndex: "tax_invoice_no",
        rowKey: "tax_invoice_no",
        width: "13%",
        render: (text, row) => {
          if (row.title && (row.title === "Title" || row.title === "Total")) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else {
            if (text) {
              return text;
            } else {
              return "-";
            }
          }
        }
      },

      {
        title: getLanguage({ id: "purchaseReturns.transactionNo" }),
        dataIndex: "id_customer",
        rowKey: "id_customer",
        width: "13%",
        render: (text, row) => {
          if (row.title && (row.title === "Title" || row.title === "Total")) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (
            row.id_sales_invoice &&
            row.transaction_type &&
            row.transaction_type === 21
          ) {
            return (
              <Link to={`${redirectRaptorSI}/update/${row.id_sales_invoice}`}>
                {row.sales_invoice_no}
              </Link>
            );
          } else if (
            row.id_sales_return &&
            row.transaction_type &&
            row.transaction_type === 21
          ) {
            return (
              <Link to={`${redirectRaptorST}/update/${row.id_sales_return}`}>
                {row.sales_return_no}
              </Link>
            );
          }

          // pos
          else if (
            row.id_sales_invoice &&
            row.transaction_type &&
            row.transaction_type === 31
          ) {
            return (
              <Link to={`${redirectPosSI}/update/${row.id_sales_invoice}`}>
                {row.sales_invoice_no}
              </Link>
            );
          } else if (
            row.id_sales_return &&
            row.transaction_type &&
            row.transaction_type === 31
          ) {
            return (
              <Link to={`${redirectPosST}/update/${row.id_sales_return}`}>
                {row.sales_return_no}
              </Link>
            );
          } else if (row.id_sales_invoice) {
            return (
              <Link to={`${redirectSI}/update/${row.id_sales_invoice}`}>
                {row.sales_invoice_no}
              </Link>
            );
          } else if (row.id_sales_return) {
            return (
              <Link to={`${redirectST}/update/${row.id_sales_return}`}>
                {row.sales_return_no}
              </Link>
            );
          } else {
            return "-";
          }
        }
      },
      // {
      //   title: "Location",
      //   dataIndex: "location_name",
      //   rowKey: "location_name",
      //   width: "20%",
      //   render: (text,row) => {
      //     if(row.transaction_date === "Title" || row.transaction_date === "Total"){
      //       return {
      //         props: {
      //           colSpan: 0,
      //         },
      //       }
      //     }
      //     else{
      //       if (text && text.length > 25) {
      //         return (
      //           <Tooltip placement="bottom" title={text}>
      //             {text.substring(0, 25) + "..."}
      //           </Tooltip>
      //         );
      //       } else {
      //         return text;
      //       }
      //     }
      //   }
      // },
      {
        title: getLanguage({ id: "customerName" }),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        width: "11%",
        render: (text, row) => {
          if (row.title && (row.title === "Title" || row.title === "Total")) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else {
            if (text && text.length > 25) {
              return (
                <Tooltip placement="bottom" title={text}>
                  {text.substring(0, 25) + "..."}
                </Tooltip>
              );
            } else {
              return text;
            }
          }
        }
      },
      {
        title: getLanguage({ id: "suppliers.add.taxNumber" }),
        dataIndex: "tax_number",
        rowKey: "tax_number",
        width: "10%",
        //align:"left",
        render: (text, row) => {
          if (row.title && (row.title === "Title" || row.title === "Total")) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else {
            if (text && text.length > 30) {
              return (
                <Tooltip placement="bottom" title={text}>
                  {text.substring(0, 30) + "..."}
                </Tooltip>
              );
            } else if (text) {
              return text;
            } else {
              return "-";
            }
          }
        }
      },
      {
        title: getLanguage({ id: "totalAmount" }),
        dataIndex: "total_amount",
        rowKey: "total_amount",
        width: "13%",
        align: "right",
        parse: true,
        render: (text, row) => {
          if (row.title && row.title === "Title") {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (row.title && row.title === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          } else if (text) {
            return <NumberFormat value={parseFloat(text)} />;
          } else {
            return "-";
          }
        }
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalTax" }),
        dataIndex: "total_tax",
        rowKey: "total_tax",
        width: "13%",
        align: "right",
        parse: true,
        render: (text, row) => {
          if (row.title && row.title === "Title") {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (row.title && row.title === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          } else if (text) {
            return <NumberFormat value={parseFloat(text)} />;
          } else {
            return "-";
          }
        }
      }
    ];
    //console.log(this.props.vatOutReport);
    // Data setup
    const {
      vat_out_reports,
      locations,
      loading,
      loadingForeign,
      filter,
      filter_select
    } = this.props.vatOutReport ? this.props.vatOutReport : [];

    let pi_title = {
      title: "Title",
      transaction_date: getLanguage({ id: "salesInvoice" })
    };
    let sales_invoice = {
      title: "Total",
      transaction_date: "Total " + getLanguage({ id: "salesInvoice" }),
      total_amount: 0,
      total_tax: 0
    };
    let pt_title = {
      title: "Title",
      transaction_date: getLanguage({ id: "salesReturns.title" })
    };
    let sales_return = {
      title: "Total",
      transaction_date: "Total " + getLanguage({ id: "salesReturns.title" }),
      total_amount: 0,
      total_tax: 0
    };
    let grand_total = {
      title: "Total",
      transaction_date: getLanguage({ id: "grandTotal" }),
      total_amount: 0,
      total_tax: 0
    };

    let dataSource = [];

    if (vat_out_reports !== null) {
      dataSource.push(pi_title);

      vat_out_reports.sales_invoices.map(data => {
        dataSource.push(data);
        sales_invoice.total_amount += parseFloat(data.total_amount);
        sales_invoice.total_tax += parseFloat(data.total_tax);
      });

      dataSource.push(sales_invoice);
      dataSource.push(pt_title);

      vat_out_reports.sales_returns.map(data => {
        dataSource.push({
          ...data,
          transaction_date: data.return_date
        });
        sales_return.total_amount += parseFloat(data.total_amount);
        sales_return.total_tax += parseFloat(data.total_tax);
      });

      dataSource.push(sales_return);

      grand_total.total_amount =
        parseFloat(sales_invoice.total_amount) -
        parseFloat(sales_return.total_amount);
      grand_total.total_tax =
        parseFloat(sales_invoice.total_tax) -
        parseFloat(sales_return.total_tax);

      dataSource.push(grand_total);
    }

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const fileName = getLanguage({ id: "vatOutReports.pageHeader" });
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="vatOutReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="vatOutReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() =>
                (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              scale={0.6}
              title="vatOutReports.pageHeader"
            />

            <ExcelWrapper
              title="vatOutReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="vatOutReports.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "100%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col
                    span={12}
                    // style={{
                    //   marginTop: "auto"
                    // }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.only" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clickFilter" />} />
            ) : this.props.vatOutReport.vat_out_reports.sales_invoices === 0 &&
              this.props.vatOutReport.vat_out_reports.sales_returns === 0 &&
              !this.props.vatOutReport.loading ? (
              <Empty
                description={<IntlMessages id="vatOutReports.table.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    //rowKey={record => record.id_cash_management}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    vatOutReport: state.vatOutReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(VatOutReport);
